	module.exports = {"data":{"post":{"html":"<p><strong>I don't use public transportation.</strong> Most novice expats will gush about how in Germany they don't need a car at all, and with bike, bus, tram, subway, commuter train, or whatever, they can get everywhere they need to go.</p>\n<p>We do have a public bus that stops in our village. During rush hour it stops every hour, during the day every two hours. That might be OK for commuting to school or work (the times are coordinated with trains to Hamburg), but it's not particularly convenient for daily life. And since there are no shops in our village at all, not even a bakery, we'd be stuck without two cars.</p>\n<p>That said, we do try to use public transportation to get to Hamburg when we can. But we drive a car to the station.</p>\n<p>(This also reminds me of my favorite German joke: <em>Alle reden vom öffentlichen Verkehr, aber keiner traut sich!</em> It can't really be translated, but <em>Verkehr</em> can mean either transportation or intercourse, so think of public <em>Verkehr</em>.)</p>\n<p>(part of a <a href=\"http://www.papascott.de/archives/category/not-an-expat/\">series</a>)</p>","fields":{"title":"I am not an Expat Blogger 5: Public Transportation","date":"24 Jul 2012","url":"https://papascott.de/2012-07-24-i-am-not-an-expat-blogger-5-public-transportation/"}}},"pathContext":{"slug":"/2012-07-24-i-am-not-an-expat-blogger-5-public-transportation/","prev":{"fields":{"title":"I am not an Expat Blogger 4: Vacation","date":"23 Jul 2012","slug":"/2012-07-23-i-am-not-an-expat-blogger-4-vacation/"}},"next":{"fields":{"title":"I am not an Expat Blogger 6: Beer","date":"25 Jul 2012","slug":"/2012-07-25-i-am-not-an-expat-6-beer/"}}}}

